*{
  margin: 0; padding: 0;
  box-sizing: border-box;
}
a{
  text-decoration: none;
  color: #000;
}
ul,ol{
  list-style: none;
}
.scroll::-webkit-scrollbar {
  display: none;
}
.scroll {
  overflow: hidden;
}
